import {createEnhancedElement} from '@servicenow/library-enhanced-element';
import styles from './_now-textarea.scss';
import {t} from 'sn-translate';
import {
	getFormFieldMessageIds,
	renderFormFieldLabel,
	renderFormFieldMessages
} from '@servicenow/now-input';
import {messagesSchema} from '@servicenow/now-input';
import focusBehavior from '@servicenow/behavior-focus';

import {actionTypes} from '@servicenow/ui-core';
const {
	COMPONENT_CONNECTED,
	COMPONENT_DOM_READY,
	COMPONENT_PROPERTY_CHANGED
} = actionTypes;

const handleInput = (e, maxlength, autoresize, updateState, dispatch) => {
	const data = e.data;
	const textarea = e.target;
	if (maxlength) {
		const currentLength = textarea.value.length || 0;
		const charsRemaining = parseInt(maxlength) - currentLength;
		updateState({charsRemaining});
	}
	if (autoresize) {
		textarea.style.height = 'auto';
		textarea.style.height = textarea.scrollHeight + 1 + 'px';
	}
	dispatch(({properties: {name}}) => {
		return {
			type: 'NOW_TEXTAREA#INPUT',
			payload: {name, data, fieldValue: textarea.value}
		};
	});
};

const handleBlur = (e, dispatch) => {
	const {valid} = e.target.validity;
	const {name, value} = e.target;
	dispatch(() => {
		return {
			type: 'NOW_TEXTAREA#INVALID_SET',
			payload: {name: name, value: !valid, fieldValue: value}
		};
	});
	dispatch(({properties}) => {
		const {disabled, readonly, invalid} = properties;
		return {
			type: 'NOW_TEXTAREA#VALUE_SET',
			payload: {name: name, value: value},
			shouldDispatch: !disabled && !readonly && !invalid
		};
	});
};

const getCharsRemaining = (dispatch, properties, updateState) => {
	const {name, value, maxlength} = properties;
	const currentLength = (value && value.length) || 0;
	const charsRemaining = parseInt(maxlength) - currentLength;
	updateState({charsRemaining});
	dispatch(() => {
		return {
			type: 'NOW_TEXTAREA#INVALID_SET',
			payload: {name: name, value: true, fieldValue: value},
			shouldDispatch: charsRemaining < 0
		};
	});
};

const renderCharacterCounter = (showCounter, maxlength, charsRemaining) => {
	if (showCounter && maxlength) {
		return (
			<div
				class={{
					'now-textarea-character-counter': true,
					'-warning': charsRemaining / parseInt(maxlength) < 0.2,
					'-error': charsRemaining <= 0
				}}
				aria-live="polite"
				aria-relevant="text"
				aria-atomic="true">
				{t('Characters left: {0}', charsRemaining)}
			</div>
		);
	}
};

const view = (state, {dispatch, updateState}) => {
	const {componentId, charsRemaining} = state;
	const {
		autoresize,
		cols,
		disabled,
		helperContent,
		hideRequiredIndicator,
		invalid,
		label,
		maxlength,
		messages,
		minlength,
		name = `textarea-${componentId}`,
		optional,
		placeholder,
		readonly,
		required,
		resize,
		rows,
		showBorders,
		showCounter,
		value
	} = state.properties;

	return (
		<div className="now-textarea">
			{renderFormFieldLabel(
				componentId,
				invalid,
				required,
				optional,
				label,
				helperContent,
				hideRequiredIndicator
			)}
			<textarea
				id={`form-field-${componentId}`}
				class={{
					'now-textarea-field': true,
					['-resize-' + (readonly ? 'none' : resize)]: resize,
					'is-invalid': invalid,
					'show-borders': showBorders
				}}
				aria-describedby={getFormFieldMessageIds(componentId, messages)}
				aria-required={required ? 'true' : 'false'}
				aria-invalid={invalid ? 'true' : 'false'}
				cols={cols}
				disabled={disabled}
				maxlength={showCounter ? undefined : maxlength}
				minlength={minlength}
				name={name}
				placeholder={placeholder}
				readonly={readonly}
				required={required}
				rows={rows}
				value={value}
				on-blur={(e) => handleBlur(e, dispatch)}
				on-input={(e) =>
					handleInput(e, maxlength, autoresize, updateState, dispatch)
				}
			/>
			{renderCharacterCounter(showCounter, maxlength, charsRemaining)}
			{renderFormFieldMessages(componentId, messages)}
		</div>
	);
};

/**
 * Form element that provides an input area for a large amount of text
 * and can include helper content, alert messages, and placeholder text.
 *
 * ```jsx
 * <now-textarea />
 * ```
 *
 * @seismicElement now-textarea
 * @summary Form element that provides an input area for a large amount of text.
 * @uib.label Textarea
 * @uib.icon textarea
 * @uib.description Form element that provides an input area for a large amount of text.
 * @uib.category primitives
 * @uib.associatedTypes global.core
 * @uib.properties label, value, placeholder, rows, maxlength, minlength, helperContent, messages, disabled, invalid, readonly, required, hideRequiredIndicator, optional, autoresize, resize, showBorders, showCounter, name, autofocus
 * @uib.actions NOW_TEXTAREA#INPUT, NOW_TEXTAREA#INVALID_SET, NOW_TEXTAREA#VALUE_SET
 * @uib.innerComponents now-button-iconic, now-icon, now-popover, now-input
 */
createEnhancedElement('now-textarea', {
	properties: {
		/**
		 * When true, focus is automatically placed inside the
		 * textarea on page load.
		 * @type {boolean}
		 * @uib.label Auto-focus
		 * @uib.description When true, focus is automatically placed inside the textarea on page load.
		 */
		autofocus: {schema: {type: 'boolean'}},
		/**
		 * When true, the textarea height automatically increases as the
		 * user types in the field.
		 * @type {boolean}
		 * @uib.label Auto-resize
		 * @uib.description When true, the textarea height automatically increases as the user types in the field.
		 */
		autoresize: {schema: {type: 'boolean'}},
		/**
		 * When true, the user cannot interact with the field
		 * or change its value. Field appears grayed out.
		 * @type {boolean}
		 * @uib.description Grays out the field and prevents interaction.
		 * @uib.defaultValue false
		 */
		disabled: {schema: {type: 'boolean'}},
		/**
		 * When defined, an iconic button with a popover appears next to the field
		 * label to display additional hint text to the user, such as
		 * password requirements or other instructions for completing the field.
		 * @type {(string|JSX)}
		 * @uib.label Helper text
		 * @uib.fieldType string
		 * @uib.description When defined, an iconic button with popover appears next to the field label for displaying additional hint text to the user, such as password requirements or other instructions for completing the field. Use plain text to allow the text to be translatable. If you do not want to add helper content, leave this property empty.
		 * @uib.translatable true
		 */
		helperContent: {},
		/**
		 * When true, the required indicator is visually hidden. Use this for
		 * forms where most fields are required, along with the `optional` flag
		 * for any fields that are not required.
		 * @type {boolean}
		 * @uib.label Hide Required Indicator
		 * @uib.description When true, the required indicator is visually hidden. Use this for forms where most fields are required, along with the `optional` flag for any fields that are not required.
		 */
		hideRequiredIndicator: {default: false, schema: {type: 'boolean'}},
		/**
		 * When true, the field is in an incorrect state,
		 * which is visually reflected in the field display.
		 * @type {boolean}
		 * @uib.description When true, the field appears visually invalid, indicating incorrect input.
		 * @uib.defaultValue false
		 */
		invalid: {manageable: true, schema: {type: 'boolean'}},
		/**
		 * Label describing the overall purpose of the textarea field.
		 * @type {string}
		 * @uib.description The label property allows you to tell users the value that should be entered in the texarea field.
		 * @uib.defaultValue Textarea
		 * @uib.translatable true
		 */
		label: {schema: {type: 'string'}},
		/**
		 * Maximum number of characters that the user can enter in the field.
		 * @type {number}
		 * @uib.label Max. characters
		 * @uib.description Maximum number of characters that the user can enter in the field
		 * @uib.typeMetadata.emptyStateBehavior UNDEFINED
		 */
		maxlength: {schema: {type: 'number'}},
		/**
		 * Array of inline alert messages that display below the field.
		 * @type {Array.<{status: ('critical'|'warning'|'positive'|'info'|'suggestion'), content: (string|JSX), icon: string}>}
		 * @uib.label Alert messages
		 * @uib.description Array of inline alert messages that display below the field.
		 * @uib.fieldType json
		 * @uib.defaultValue []
		 */
		messages: {
			default: [],
			schema: messagesSchema
		},
		/**
		 * Minimum number of characters that the user must enter in the field.
		 * @type {number}
		 * @uib.label Min. characters
		 * @uib.description Minimum number of characters that the user must enter in the field.
		 * @uib.typeMetadata.emptyStateBehavior UNDEFINED
		 */
		minlength: {schema: {type: 'number'}},
		/**
		 * Name of the field control. Dispatched along with the field value
		 * on user interaction.
		 * @type {string}
		 * @uib.label Identifier
		 * @uib.description The name property allows you to set a reference name for the input field which will be included in dispatched actions on user interactions.
		 */
		name: {schema: {type: 'string'}},
		/**
		 * When true, the field is optional and does not need to be completed by
		 * the user before completing the form.
		 * @type {boolean}
		 * @uib.description This property allows you to indicate that the user does not have to enter a value in the input field before proceeding.
		 */
		optional: {schema: {type: 'boolean'}},
		/**
		 * Hint text displayed in the textarea when the user has not yet input
		 * a value.
		 * @type {string}
		 * @uib.label Placeholder text
		 * @uib.description The placeholder property allows you to set a placeholder text for the input field. This text should hint to the user what input is expected.
		 * @uib.translatable true
		 */
		placeholder: {schema: {type: 'string'}},
		/**
		 * When true, the field appears enabled but the user cannot modify the value.
		 * @type {boolean}
		 * @uib.label Read-only
		 * @uib.description By enabling this property, the user cannot modify the value of the field. The field is not disabled in this setting.
		 */
		readonly: {schema: {type: 'boolean'}},
		/**
		 * When true, the user must enter a value in the
		 * textarea before they can complete the form.
		 * @type {boolean}
		 * @uib.description This property allows you to indicate that the user must enter a value in the field before proceeding.
		 */
		required: {schema: {type: 'boolean'}},
		/**
		 * Determines whether the field can be resized vertically, horizontally,
		 * both directions, or neither direction.
		 * @type {('both'|'horizontal'|'none'|'vertical')}
		 * @uib.description Controls in which directions the field can be resized
		 */
		resize: {
			default: 'vertical',
			schema: {
				type: 'string',
				enum: ['both', 'horizontal', 'none', 'vertical']
			}
		},
		/**
		 * Height of the textarea, in characters.
		 * @type {number}
		 * @uib.label Height (rows)
		 * @uib.description Height of the textarea, in characters.
		 * @uib.typeMetadata.emptyStateBehavior UNDEFINED
		 */
		rows: {schema: {type: 'number'}},
		/**
		 * When true, the field appears with borders on all sides.
		 * By default, the field appears with only a bottom border.
		 * @type {boolean}
		 * @uib.description Indicates whether the field appears with borders on all sides or whether it is only displayed with a bottom border.
		 */
		showBorders: {schema: {type: 'boolean'}},
		/**
		 * When true, displays a character counter underneath the
		 * textarea to show the user the number of remaining characters
		 * between the number added and the value of the `maxlength` property. When set and the user
		 * types past `maxlength`, the counter goes below 0 and the
		 * field is invalid.
		 * @type {boolean}
		 * @uib.description Indicates whether to display a character counter underneath the textarea to show the user the number of remaining characters between the number added and `maxlength`
		 */
		showCounter: {schema: {type: 'boolean'}},
		/**
		 * The value of the textarea field.
		 * @type {string}
		 * @uib.description The value of the textarea field
		 * @uib.translatable true
		 */
		value: {manageable: true, schema: {type: 'string'}}
	},
	actionHandlers: {
		[COMPONENT_CONNECTED]: ({properties, dispatch, updateState}) => {
			if (properties.maxlength) {
				getCharsRemaining(dispatch, properties, updateState);
			}
		},
		[COMPONENT_DOM_READY]: ({host, properties}) => {
			const textarea = host.shadowRoot.querySelector('textarea');
			if (!textarea) return;
			if (properties.autofocus) {
				textarea.focus();
			}
			if (properties.autoresize) {
				textarea.style.height = textarea.scrollHeight + 1 + 'px';
			}
		},
		[COMPONENT_PROPERTY_CHANGED]: ({
			action,
			properties,
			dispatch,
			updateState
		}) => {
			const {name, value} = action.payload;
			if (
				(name === 'value' && properties.maxlength) ||
				(name === 'maxlength' && value)
			) {
				getCharsRemaining(dispatch, properties, updateState);
			}
		}
	},
	dispatches: {
		/**
		 * Dispatched when the field is blurred. Payload contains the `name`
		 * of the field, the current validity of the field determined by the `value` of
		 * the invalid property based on the `required` and `pattern` properties,
		 * and the current `fieldValue` inside of the input.
		 * Set the `manage-invalid` property to override the default behavior and
		 * handle this action manually.
		 * @type {{name: string, value: boolean, fieldValue: string}}
		 * @uib.description Dispatched when the field is blurred.
		 */
		'NOW_TEXTAREA#INVALID_SET': {
			schema: {
				type: 'object',
				properties: {
					name: {type: 'string'},
					value: {type: 'boolean'},
					fieldValue: {type: 'string'}
				}
			}
		},
		/**
		 * Dispatched when the textarea is blurred and the value is valid.
		 * Payload contains the updated `value` and the `name` of the field.
		 * Set the `manage-value` property to override the default behavior and
		 * handle this action manually.
		 * @type {{name: string, value: string}}
		 * @uib.description Dispatched when the textarea is blurred and the value is valid.
		 */
		'NOW_TEXTAREA#VALUE_SET': {
			schema: {
				type: 'object',
				properties: {
					name: {type: 'string'},
					value: {type: 'string'}
				}
			}
		},
		/**
		 * Dispatched when the user enters data in the textarea by typing or
		 * pasting text. Internal validation is not performed and the actual
		 * `value` property is not updated at this point. Payload contains the
		 * `name` and the current `fieldValue` of the textarea, as well as the
		 * `data` of the native `input` event (usually a typed character).
		 * @type {{name: string, data: string, fieldValue: string}}
		 * @uib.description Dispatched when the user enters data in the textarea by typing or pasting text.
		 */
		'NOW_TEXTAREA#INPUT': {
			schema: {
				type: 'object',
				properties: {
					name: {type: 'string'},
					data: {type: 'string'},
					fieldValue: {type: 'string'}
				}
			}
		}
	},
	behaviors: [
		{
			behavior: focusBehavior
		}
	],
	styles,
	view
});
